.content-pagina-plataform{
display: flex;
}

.SectionD{
    display: flex;
    flex-direction: column;
}

.SectionD::before{
    content: "";
    height: 2px;
    width: 100%;
    background: linear-gradient(to right, transparent 0%, #828282, #999595, transparent 100%);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}


.Header-bar-plataform-content-princip{    
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: space-around;
    align-items: center;
}