.content-header-barplataform{
       position: absolute;
    top: 30px;
    right: 50px;
    display: flex;
    width: 70%;
    justify-content: space-between;
    align-items: center;
}
.explorer{
    display: flex;
    align-items: center;
    border-radius: var(--br-86xl);
    background-color: var(--color-dimgray);
    width: 680px;
    height: 47px;
}

.explorer i{
    font-size: var(--font-size-base);
    line-height: 160%;
    font-family: var(--font-montserrat);
    color: var(--color-darkgray-200);
    text-align: left;
}